.habilidades-container{
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-content: center;
    align-items: center;
    background-color: cornflowerblue;
    justify-content: space-around;
}

.barrinha-container{
    border: 2px solid white;
    border-radius: 30px;
    flex: 1;
    display: flex;
    flex-direction: row;
}

.habilidade-container{
    display: flex;
    flex-direction: row;
    width: 100%;
}

.preechimento-barrinha{
    background-color: aliceblue;
    border-radius: 30px;
}

.margem-habilidade{
    margin: 5px;
}